import React, {useState, useEffect} from 'react'
import {LinkContainer} from 'react-router-bootstrap'
import {Table, Button, Row, Col} from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import Loader from "../components/Loader";
import Message from "../components/Message";
import {listProducts, deleteProduct, createProduct} from '../actions/productActions'
import {PRODUCT_CREATE_RESET} from '../constants/productConstants'

function ProductListPage({history, match}) {

  const dispatch = useDispatch()
  const productList = useSelector(state => state.productList)
  const {loading, error, products} = productList

  const userLogin = useSelector(state => state.userLogin)
  const {userInfo} = userLogin

  const productDelete = useSelector(state => state.productDelete)
  const {loading: loadingDelete, error: errorDelete, success: successDelete} = productDelete

  const productCreate = useSelector(state => state.productCreate)
  const {loading: loadingCreate, error: errorCreate, success: successCreate, product: createdProduct} = productCreate

  useEffect(() => {
    dispatch({type: PRODUCT_CREATE_RESET})

    if (!userInfo.isAdmin) {
      history.push('/login')
    }

    if (successCreate) {
      history.push(`/admin/product/${
        createdProduct._id
      }/edit`)
    } else {
      dispatch(listProducts())
    }
  }, [
    dispatch,
    history,
    userInfo,
    successDelete,
    successCreate,
    createdProduct
  ])

  const deleteHandler = (id) => {
    if (window.confirm('Are you sure you want to delete this product?')) {
      dispatch(deleteProduct(id))
    }
  }

  const createProductHandler = (product) => {
    dispatch(createProduct())
  }

  return (
    <div>
      <Row className='align-items-center'>
        <Col>
          <h1>Products</h1>
        </Col>

        <Col className='d-flex justify-content-end'>
          <Button className='my-3'
            onClick={createProductHandler}>
            Create Products &nbsp;&nbsp;<i className='fas fa-plus'></i>
          </Button>
        </Col>
      </Row>
      {
      loadingDelete && <Loader/>
    }
      {
      errorDelete && <Message variant='danger'>
        {errorDelete}</Message>
    }

      {
      loadingCreate && <Loader/>
    }
      {
      errorCreate && <Message variant='danger'>
        {errorCreate}</Message>
    }

      {
      loading ? <Loader/>: error ? <Message variant='danger'>
        {error}</Message> : (
        <Table striped bordered hover responsive className='table-sm'>
          <thead className='table-dark'>
            <tr>
              <th>ID</th>
              <th>NAME</th>
              <th>PRICE</th>
              <th>RELEASED</th>
              <th style={
                {width: "10%"}
              }></th>
            </tr>
          </thead>

          <tbody className='table-secondary'>
            {
            products ? (products.map(product => (
              <tr key={
                product._id
              }>
                <td> {
                  product._id
                }</td>
                <td> {
                  product.name
                }</td>
                <td> {
                  product.price
                }</td>
                <td> {
                  product.isReleased ? (
                    <i className='fas fa-check'
                      style={
                        {color: 'green'}
                    }></i>
                  ) : (
                    <i className='fas fa-times'
                      style={
                        {color: 'red'}
                    }></i>
                  )
                }</td>
                <td className='d-flex justify-content-around'>
                  <LinkContainer to={
                    `/admin/product/${
                      product._id
                    }/edit`
                  }>
                    <Button variant='light' className='btn-sm'>
                      <i className='fas fa-edit'></i>
                    </Button>
                  </LinkContainer>

                  <Button variant='danger' className='btn-sm'
                    onClick={
                      () => deleteHandler(product._id)
                  }>
                    <i className='fas fa-trash'></i>
                  </Button>
                </td>
              </tr>
            ))) : ('')
          } </tbody>
        </Table>
      )
    } </div>
  )
}

export default ProductListPage
